import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { SpecialistServiceProvider } from '../providers/specialist-service/specialist-service';
import { HttpModule } from '@angular/http';
import { DomainServiceProvider } from '../providers/domain-service/domain-service';
import { SpecialityServiceProvider } from '../providers/speciality-service/speciality-service';
import { UserServiceProvider } from '../providers/user-service/user-service';
import { MessageServiceProvider } from '../providers/message-service/message-service';
import { RateServiceProvider } from '../providers/rate-service/rate-service';
import { SpecialistDetailServiceProvider } from '../providers/specialist-detail-service/specialist-detail-service';

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    HttpModule ,
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/contact/contact.module#ContactPageModule', name: 'ContactPage', segment: 'contact', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module#HomePageModule', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/list/list.module#ListPageModule', name: 'ListPage', segment: 'list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/profile/profile.module#ProfilePageModule', name: 'ProfilePage', segment: 'profile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/register.module#RegisterPageModule', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tabs/tabs.module#TabsPageModule', name: 'TabsPage', segment: 'tabs', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/specialist-list/specialist-list.module#SpecialistListPageModule', name: 'SpecialistListPage', segment: 'specialist-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/specialist-detail/specialist-detail.module#SpecialistDetailPageModule', name: 'SpecialistDetailPage', segment: 'specialist-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/specialities/specialities.module#SpecialitiesPageModule', name: 'SpecialitiesPage', segment: 'specialities', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    SpecialistServiceProvider,
    DomainServiceProvider,
    SpecialityServiceProvider,
    UserServiceProvider,
    MessageServiceProvider,
    RateServiceProvider,
    SpecialistDetailServiceProvider
  ]
})
export class AppModule {}



